

.term{
    background: linear-gradient(rgba(230, 225, 225, 0.726),lightcyan);
    height: 720vh;
padding-top: 80px;
   
}
.termreal{
    width: 60%;
    margin: 0 auto;
}


.termserv{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.termserv h2{
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    color: rgb(0, 0, 0);
    margin: 0;
    margin-bottom: 10px;
    padding: 0;
}

.termserv hr{
    width: 10%;
    border: 1px solid #28a745 ;
    margin-bottom: 80px;
}


