.workerrs{
    width: 60%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.workerrs h2{
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    color: white;
    margin: 0;
    margin-bottom: 10px;
    padding: 0;
}
.workerrs p{
    background: linear-gradient(lightblue,lightcyan);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow:  0 -10px 20px #fff;
    font-size: 30px;
    text-align: center;
    font-weight: 800;
    margin-bottom: 100px;
}

.moreWorkerr button{
    margin-top: 50px;
    border: none;
    outline: none;
    background: #28a745;
    color: white;
    padding: 15px 50px;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.6s ease-in-out;
}
.moreWorkerr button:hover{
    box-shadow: 0 2px 10px #ffffff;
    background: white;
    color: #28a745;
   
}

.workerrHero{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 40px; 
}

.conworkerr{
    width: 280px;
    height: 520px;
    background: transparent;
    border: 0;
    display: flex;
    flex-direction: column;
    align-items: center !important;
    box-shadow:  0 5px 10px #424242  ;
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 0;
    cursor: pointer;
    overflow: hidden ;
    
    
}
 .workerrsimage:hover{
    transition: all 1s ease-in-out;
   transform: scale(1.1);
   opacity: 0.7;
}

.conworkerr h2{
  font-size: 25px;
  color: black;
  margin: 10px 0;
}
.conworkerr h3{
  font-size: 20px;
  color: #352b2b;
  margin: 5px 0;
  text-align: center;
  font-weight: 800;
  text-shadow: 0 10px 20px #ffffff;
  
}
.conworkerr h4{
  font-size: 20px;
  color: #352b2b;
  margin: 2px 0;
  text-align: center;
  font-weight: 800;
  text-shadow: 0 10px 20px #ffffff;
}
.conworkerr h5{
  font-size: 20px;
  color: #352b2b;
  margin: 2px 0;
  text-align: center;
  font-weight: 800;
  text-shadow: 0 10px 20px #ffffff;
}

.btnhewerr{
    margin-top: 20px;
    color: white;
    border: none;
    outline: none;
    padding: 10px 30px;
    margin-right: 20px;
    border-radius: 20px;
    font-size: 20px;
    cursor: pointer;
}
.btnhewerr p{
  color: white;
}
.btnhewerr:hover{
  opacity: 0.8;
}

.btnrewerr{
    background: #28a745;
}

.workerrsimage{
    width: 100%;
}









