
.detail{
    background-image: url('../../component/Assets/detail.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    height: 90vh;
display: flex;
align-items: center;
justify-content: center;
}

.bgDetail{
 width: 80%;
 margin: 0 auto;
 height: 67vh;
 background: white;
 box-shadow: 10px -10px 20px #fff;
 display: flex;
 flex-direction: column;
 margin-top: 50px;
}


.theRest{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
   width: 80%;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 0;
    gap: 20px;
}

.skillOne{
    display: flex;
    padding-top: 0;
   width: 80%;
   margin: 0 auto;
   margin-top: 20px;

}
.skillOne h4{
 margin-right: 50px;

}


.allInfoWorkk{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
  
}

.religion{
    width: 400px;
    display: flex;
   justify-content: space-between;
    gap: 70px;
    border-top: 1px solid #42424234;
    
}

.religion:last-child{
    border-bottom: 1px solid #42424234; 
}

.religion p{
    color: #4242426e;
}
.religion h4{
    color: #424242d7;
}




.conworkerrr{
    
    
    border: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    padding: 0;
    cursor: pointer;
    overflow: hidden ;
    margin-top: -220px;
    
}
 .workerrrsimage:hover{
    transition: all 1s ease-in-out;
   transform: scale(1.1);
   opacity: 0.7;
}

.conworkerrr h2{
  font-size: 25px;
  color: black;
  margin: 10px 0;
  margin-top: 30px;
}
.conworkerrr h3{
  font-size: 20px;
  color: #352b2b;
  margin: 5px 0;
  text-align: center;
  font-weight: 800;
  text-shadow: 0 10px 20px #ffffff;
  
}
.conworkerrr h4{
  font-size: 20px;
  color: #352b2b;
  margin: 2px 0;
  text-align: center;
  font-weight: 800;
  text-shadow: 0 10px 20px #ffffff;
}
.conworkerrr h5{
  font-size: 20px;
  color: #352b2b;
  margin: 2px 0;
  text-align: center;
  font-weight: 800;
  text-shadow: 0 10px 20px #ffffff;
}

.btnhewerrr{
    margin-top: 20px;
    color: white;
    border: none;
    outline: none;
    padding: 10px 30px;
    margin-right: 20px;
    border-radius: 20px;
    font-size: 20px;
    cursor: pointer;
}
.btnhewerrr p{
  color: white;
}
.btnhewerrr:hover a{
  opacity: 0.8;
  color: #28a745;

}

.btnrewerrr{
    background: #28a745;
}
.btnhewerrr a{
   color: white;
   text-decoration: none;
   text-transform: capitalize;
}

.workerrrsimage{
   width: 50%;
    border-radius: 50%;
}



